/*
 *  Yandex Direct
 *
 */
var

    //  
    _modulePrefix = 'YaDirect',

    directPlaceId = '29789',

    libSrc = '//an.yandex.ru/system/context.js',

    //    
    adsElementsClasses = 'ya-direct-root',

    // -  
    adsLoadedElementsClasses = 'ya-direct_loaded',

    //  
    initLib = () => {

        //  
        $.getScript(libSrc);

    },

    //  STAT_ID
    getStatId = (param) => {

        var stat_store = null,
            screenInfo = require('../ScreenViewer').getScreenInfo(),
            screenInfoMap = {
                mobile: 1,
                tablet: 2,
                tabletLandscape: 3,
                desktop: 4,
                desktopFull: 5
            };

        /*
		 *  PlaceId (1-99)  (0)    (1-5)  (0) ID  (1-99)

			  :
			1 - 
			2 - 
			3 - ()
			4 - 
			5 - 

			: 250501

			   16 777 215 https://yandex.ru/support/partner/products-direct/variables-description.xml
		*
		*/

        stat_store = [
            param.placeAdsId.replace('ads', ''), //  #ads
            0, // 
            screenInfoMap[screenInfo.type], //  
            0, // 
            1 // ID 
        ];

        /*stat_store = [
            param.mobile,
            param.tablet,
            param.tabletLandscape,
            param.desktop,
            param.desktopFull
        ];*/

        //return `PlaceId0${stat_store.join('')}`;
        return `${stat_store.join('')}`;

    },

    //  
    runBanners = ($place, banners) => {

        $.each(banners, function(index, banner) {

            var prop = banner.yadirect;

            //   , 
            if(!!prop) {

                prop = JSON.parse(prop);

                //      
                var $direct = $('<div />'),
                    directElId = `yaDirect_${Math.random().toString(36).substring(7)}`;

                //  
                $direct.attr({
                    'id': directElId,
                    'class': `ya-direct-${prop.type} ${adsElementsClasses} ${adsLoadedElementsClasses}`
                });

                //  
                $place.append($direct);

                //   
                window.yandex_context_callbacks = window.yandex_context_callbacks || [];

                //  
                // Stat_id
                prop.stat_id = getStatId({
                    placeAdsId: $place.attr('id'),
                    param: banner
                });

                //  
                window.yandex_context_callbacks.push(function() {
                    Ya.Direct.insertInto(directPlaceId, directElId, prop);
                });

            }

        });



        /*{
            stat_id: 1,
            ad_format: "direct",
            type: "posterHorizontal",
            limit: 2,
            favicon: true,

            font_size: .9,
            font_family: 'arial',
            border_type: 'block',
            header_bg_color: 'D7D7D7',
            border_color: 'D7D7D7',
            title_color: '990000',
            bg_color: 'ffffff',
            url_color: '006699',
            all_color: '000000',
            text_color: '000000',
            hover_color: '0066FF'
        }*/

    },

    //  
    setPlaces = (topic, places) => {

        for(let place in places) {

            let id = `#ads${place}`,
                $place = $(id);

            if($place.length) {

                //  
                //    
                runBanners($place, places[place]);
            }
        };

        //    
        // 
        initLib();
    },

    /*
     *   
     *
     */

    init = () => {

        RG.events.subscribe(`${_modulePrefix}.places.set`, setPlaces);
    };

// Add to global scope
module.exports = {
    _modulePrefix,
    init
};