var template = require('../b-select.ihtml'), //  

    // ,   
    FormTools = require('../../b-form/scripts/FormTools'),

    Select = Ractive.extend(

        FormTools,

        {
            template,        
            data() {
                return {
                    items: [],
                    selectedOption: null,
                    name: 'select',
                    isObject: true,
                    highlight: true,
                    highlighted: false
                };
            },    
            oninit() {
                RG.logger.debug('Select');

                var self = this,
                    items = this.get('items'),
                    isObject = null;

                if ($('rg-select').hasClass('js-ask-form-select')) {
                    $.ajax({
                        url: 'https://pravo.rg.ru/ask/?format=json',
                        success: function(data) {
                            var itemsDefault = self.get('items'),
                                itemsNew = data.rubrics,
                                itemsList = null;

                            itemsList = itemsDefault.concat(itemsNew);

                            self.set('items', itemsList);
                        },
                        error: function(data) {
                            console.log(data);
                        }
                    });
                }
            },

            onrender() {

                var items = this.get('items');
                //isObject = _.some(items, item => { return !item.value});

                // ,            
                this.set('isObject', _.isObject(items[0]));

                /**
                 *
                 *  
                 */
                this.on({
                    'select': (event) => {    
                        var item = this.get('selectedOption'),
                            form = this.get('form'),
                            name = this.get('name');
    
                        //     
                        //    (  ,  <option value="null")
                        if (item) {

                            //    ,
                            //       
                            if(item.href && item.href !== '#') {

                                document.location.href = item.href;

                            //    
                            //  
                            } else if(item.topic) {

                                RG.events.publish(item.topic, item);

                            //    
                            // ,     
                            } else {

                                RG.events.publish(`form.${form}.update.${name}`, item);

                            }

                            //     ,  
                            // -  
                            //    ,     value -  [object Object]
                            //   ,      - ,   
                             // if (!!this.get('value')) {
                                this.set('value', item);
                             // }

                        }       
                        event.original.preventDefault();
                    }
                });
    
            },
    
            check() {

                console.log(this);
    
                if(this.get('required')) {    
                    return this.get('value'); 
                }
    
                return true;
    
            }
        }

    );

module.exports = Select;