    //  
    initCounters = () => {

        //   
        $.each(counters, function(index, counter){

            counter.init(counter.targetId);

        });

    },

    /*
     *   
     *
     */

    init = () => {
        RG.events.subscribe(`${_modulePrefix}.run`, initCounters);
    };

//  
module.exports = {
    init
}