            //customGaURI = RG.meta.getGaURI();
            customGaURI = RG.meta.getProjectUri();

    	//   
    	window.ga = window.ga || function(){ 
    		ga.q = ga.q || [];
    		ga.q.push(arguments);
    	};

    	ga.l = +new Date;

        RG.logger.log('ga id: ' + customGaId);
        RG.logger.log('ga GaURI: ' + customGaURI);

    	//  
        ga('create', _gaId, 'rg.ru');

